.btn {
    display: inline-flex;
    align-items: center;
    :is(.icon, .badge, .avatar) {
        flex: 0 0 auto;
    }
    &-lg {
        min-width: 200px;
    }
    &-primary,
    &-tertiary,
    &-quaternary,
    &-quinary,
    &-gray-550 {
        color: var(--white);
        .icon {
            fill: var(--white);
        }
        &:is(:hover, :focus, :active) {
            color: var(--white);
        }
    }
}

.btn-close {
    display: inline-flex;
    flex: 0 0 auto;
    margin: 0;
    padding: 0;
    width: 2rem;
    height: 2rem;
    background: unset;
    .icon {
        width: 100%;
        height: 100%;
        fill: rgba(var(--white-rgb), 1);
    }
}

.btn-menu {
    display: inline-flex;
    flex: 0 0 auto;
    margin: 0;
    padding: 0;
    width: 2rem;
    height: 2rem;
    background: unset;
    .icon {
        width: 100%;
        height: 100%;
        fill: rgba(var(--white-rgb), 1);
    }
}

.btn-icon {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 auto;
    @include border-radius($border-radius-pill);
    padding: 0.5rem;
    width: 2.75rem;
    height: 2.75rem;
    background: var(--gray-550);
    .icon {
        fill: rgba(var(--white-rgb), 1);
    }
    .badge {
        position: absolute;
        top: -0.25rem;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        @include border-radius($border-radius-pill);
        width: 1rem;
        height: 1rem;
        background: var(--quaternary);
        @include font-size(0.5rem);
        &.left {
            inset-inline-start: -0.25rem;
        }
        &.right {
            inset-inline-end: -0.25rem;
        }
    }
}

.btn-clear {
    background: unset;
    .icon {
        fill: var(--quaternary);
    }
    .image & {
        margin: 0;
        padding: 0.25rem;
        width: 1.5rem;
        height: 1.5rem;
        background: var(--quaternary);
        .icon {
            width: 100%;
            height: 100%;
            fill: var(--white);
        }
    }
}

.btn-file {
    display: inline-flex;
    align-items: center;
    border: 0;
    padding: 0;
    font-style: italic;
    text-decoration: underline;
    color: var(--primary);
    .icon {
        margin-inline-end: 0.25rem;
        width: 1.25rem;
        height: 1.25rem;
        fill: var(--primary);
    }
    &:is(:hover, :focus, :active) {
        color: var(--primary);
    }
}

.btn-user {
    display: inline-flex;
    align-items: center;
    margin: 0;
    border: 0;
    padding: 0;
    text-align: start;
    @include media-breakpoint-up(sm) {
        width: 200px;
    }
    .avatar {
        @include border-radius($border-radius-pill);
        width: 2.75rem;
        height: 2.75rem;
        background: var(--gray-550);
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
        &.team {
            padding: 0.25rem;
        }
    }
    .text {
        display: none;
        flex-direction: column;
        :is(&, .name, .status) {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
        }
        @include media-breakpoint-up(sm) {
            display: flex;
        }
        .status {
            color: var(--gray-550);
            @include font-size(0.875rem);
        }
    }
    .icon {
        fill: rgba(var(--white-rgb), 1);
    }
}

.btn-back {
    padding-inline-start: 1.25rem;
    .icon {
        width: 1rem;
        height: 1rem;
        fill: var(--white);
    }
    .text {
        margin-inline-start: 0.25rem;
    }
}

.btn-upload {
    position: absolute;
    right: 0;
    bottom: 0;
}

.btn-collapse {
    display: inline-flex;
    align-items: center;
    margin: 0;
    padding: 0;
    .form-check-input {
        margin: 0;
        margin-inline-start: 0.5rem;
        border: 0;
        @include border-radius($border-radius-pill);
        padding: 0;
        float: none;

        $form-select-indicator-color: $white;
        $form-select-indicator: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' height='24px' viewBox='0 0 24 24' width='24px' fill='#{$form-select-indicator-color}'><path d='M0 0h24v24H0z' fill='none'/><path d='M19 13h-6v6h-2v-6H5v-2h6V5h2v6h6v2z'/></svg>");
        background-color: $tertiary;
        background-image: escape-svg($form-select-indicator);
        color: $pagination-color;
    }
    .form-check-input:checked[type="checkbox"] {
        $form-select-indicator-color: $white;
        $form-select-indicator: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' height='24px' viewBox='0 0 24 24' width='24px' fill='#{$form-select-indicator-color}'><path d='M0 0h24v24H0V0z' fill='none'/><path d='M19 13H5v-2h14v2z'/></svg>");
        background-color: $quaternary;
        background-image: escape-svg($form-select-indicator);
        color: $pagination-color;
    }
    label {
        cursor: pointer;
    }
}
